@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');

* { box-sizing: border-box; }
:root { font-family: 'Open Sans', sans-serif; }
body { margin: 0; }
h1,h2,h3 { font-weight: bold; }
.poolRight{
  justify-content: end;
}
.poolLeft{
  justify-content: start;
}
/*.d-flex{*/
/*  display: flex;*/
/*}*/
.appWrapper{
  display: grid;
  
  grid-template-areas: 
      "header header header"
      "menu content content"
      "footer footer footer";
      /* grid-template-areas: "header" " menu content"; */
    grid-template-rows: 80px 3fr;
    grid-template-columns: 2fr 10fr;
     grid-gap: 0.1em;
    
    ;
}

ul.poolRight{
  display: flex;
}
 ul li{
  list-style: none;
}
.wrappContent{
  grid-area: content;
  margin: 0 auto;
  background: #d395f4;
  width: 100%;
  /* padding: 20px 30px; */
}
.appWrapperContent{
  background: #d280f8;
}